import React from 'react';
import { SidebarBtn } from '../../UI/sidebarBtn/SidebarBtn'
import { LogoTitle } from '../LogoTitle/LogoTitle';
import { Link } from 'react-router-dom';
import { useContext } from 'react';
import { NewContext } from '../../context';

export const Sidebar = ({scale, changeScale, activeId, change, logoText}) => {
    const classList = ["sidebar__btns"]

    const handleChangeScale = () => {
        changeScale(!scale)
    }

    return(
        <React.Fragment>
            <LogoTitle logoText={logoText}/>
            <div className={classList.join(" ")}>
                <Link to="/"><SidebarBtn activeId={activeId} change={change} id={1} name="stats-chart-outline" text="Главная"/></Link>
                <Link to="/premium"><SidebarBtn activeId={activeId} change={change} id={2} name="cash-outline" text="Премиум"/></Link>
                <Link to="/partnership"><SidebarBtn activeId={activeId} change={change} id={3} name="person-add-outline" text="Партнерская программа"/></Link>
                <Link to="/faq"><SidebarBtn activeId={activeId} change={change} id={4} name="chatbox-ellipses-outline" text="FAQ"/></Link>
                <Link to="/contacts"><SidebarBtn activeId={activeId} change={change} id={5} name="mail-outline" text="Контакты"/></Link>
                <Link to="/profile"><SidebarBtn activeId={activeId} change={change} id={6} name="person-outline" text="Профиль"/></Link>
            </div>
            <div className='change__scale' onClick={handleChangeScale}>
                <ion-icon name="chevron-back-outline"></ion-icon>
            </div>
        </React.Fragment>
    )
};