import React, { useState, useEffect } from 'react';
import { ThemeHandler } from '../../UI/theme-handler/ThemeHandler';
import { Link } from 'react-router-dom';
import { border } from '@mui/system';
import { Button } from '@mui/material';
import { useContext } from 'react';
import { IsUserAuth } from '../../context';

export const Header = ({theme, change}) => {

    const {isUserAuth, setIsUserAuth} = useContext(IsUserAuth)

    return(
        <div className='header'>
            <ThemeHandler change={change}/>
            <div className="header__btns">
                {!isUserAuth && 
                    <div className='header__login__register'>
                        <Button style={{ marginRight: "10px" }} variant="contained">
                            <Link
                            to="/login"
                            style={{ 
                                textDecoration: "none",
                                color: "#53B9EA",
                                fontFamily: "'Inria Sans', sans-serif",
                                fontSize: "14px",
                                textAlign: "center",
                                color: "#ffffff",
                                textTransform: "capitalize",
                                width: "100%"
                            }}>Вход</Link>
                        </Button>
                        <Button variant="outlined">
                            <Link
                            to="/register"
                            style={{ 
                                textDecoration: "none",
                                color: "#53B9EA",
                                fontFamily: "'Inria Sans', sans-serif",
                                fontSize: "14px",
                                textAlign: "center",
                                color: "#53B9EA",
                                textTransform: "capitalize",
                                width: "100%"
                            }}>Регистрация</Link>
                        </Button>
                    </div>
                }
                {isUserAuth &&
                <div className='header__login__register'>
                    <Button variant="outlined" onClick={()=>{setIsUserAuth(false)}}>
                        <Link
                        to="/"
                        style={{ 
                            textDecoration: "none",
                            color: "#53B9EA",
                            fontFamily: "'Inria Sans', sans-serif",
                            fontSize: "14px",
                            textAlign: "center",
                            color: "#53B9EA",
                            textTransform: "capitalize",
                            width: "100%"
                        }}>Выход</Link>
                    </Button>
                </div>
                }
            </div>
        </div>
    )
};