import React from 'react';
import classes from './Partnership.module.css';
import { TextField } from "@mui/material"
import { useRef } from 'react';
import { NewContext } from '../../context';
import { useContext } from 'react';

export const Partnership = () => {

    return(
        <div className={classes.partnership__main}>
            <div className={classes.partnership__info}>
                <div className={[classes.value, classes.box__shadow].join(" ")}>
                    <div className={classes.value__title}>Рефералы</div>
                    <div className={classes.value__number}><div>10</div></div>
                </div>
                <div className={[classes.personal__link, classes.box__shadow].join(" ")}>
                    <div className={classes.link__title}>Ваша ссылка</div>
                    <div className={classes.link}>
                        <TextField className='user__info__point' size='small' value="crypto-parser.ru/invite/code/fddsf32fwefewfew" id="outlined-basic" variant="outlined" />
                    </div>
                </div>
                <div className={[classes.profit, classes.box__shadow].join(" ")}>
                    <div className={classes.profit__title}>Профит</div>
                    <div className={classes.profit__number}>700 р.</div>
                </div>
            </div>
            <div className={classes.referals__main}>
                <div className={[classes.partnership__referals, classes.box__shadow].join(" ")}>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                    <div className={classes.user__referal}>
                        <div className={classes.user__name}>@tgname</div>
                        <div className={classes.user__profit}>100 р.</div>
                    </div>
                </div>
                <div className={[classes.manual, classes.box__shadow].join(" ")}>
                    <div className={classes.manual__title}>Как это работает?</div>
                    <div className={classes.manual__steps}>
                        <div className={classes.manual__step}>1.-----------------------------------------------</div>
                        <div className={classes.manual__step}>2.-----------------------------------------------</div>
                        <div className={classes.manual__step}>3.-----------------------------------------------</div>
                        <div className={classes.manual__step}>4.-----------------------------------------------</div>
                    </div>
                </div>
            </div>
        </div>
    )
};