import React from 'react';
import { useState } from 'react';
import classes from './UserSub.module.css'

export const UserSub = () => {

    const [isSub, setIsSub] = useState(true)

    const Sub = () => {
        return(
            <div className={classes.sub__inactive}>
                <div className={classes.status}>
                    {/* <div>статус</div> */}
                    { isSub == true && <div className={classes.active}>активна</div>}
                    { isSub == false && <div className={classes.inactive}>неактивна</div>}
                    <div style={{display: 'flex', alignItems: 'center', textTransform: 'lowercase'}}>
                        <ion-icon name="timer-outline"></ion-icon>
                        <span style={{marginLeft: '10px'}}>5 д.</span>
                    </div>
                </div>
            </div>
        )
    }

    return(
        <React.Fragment>
            <div className='subscribe__info'>
                <div className='profile__title'>подписка</div>
                <Sub />
            </div>
        </React.Fragment>
    )
};