import React, { useContext, useState } from 'react';
import '../css/index.css';
import { Sidebar } from '../components/Sidebar/Sidebar';
import { useTheme } from '../hooks/use-theme';
import { Header } from '../components/Header/Header';
import { Profile } from '../components/Profile/Profile';
import { ErrorPage } from './ErrorPage';
import {Contacts} from '../components/Contacts/Contacts'
import { Premium } from '../components/Premium/Premium';
import { Partnership } from '../components/Partnership/Partnership';
import { Login } from '../components/LoginRegister/Login';
import { Link } from 'react-router-dom';
import { Register } from '../components/LoginRegister/Register';
import { IsUserAuth } from '../context';
import { Home } from '../components/Home/Home';



export const AllRouters = ({page}) => {

    const { theme, setTheme } = useTheme()
    const [activeId, setActiveId] = useState(1)
    const [scaleSidebar, setScaleSidebar] = useState(true)
    const [isUserAuth, setIsUserAuth] = useState(true)
  
    const handleThemeClick = () => {
      if(theme === "dark") {
        setTheme("light")
      } else {
        setTheme("dark")
      }
    }
  
    const HandleScaleSidebar = () => {
      if(scaleSidebar) {
        return(
          <div className='sidebar'>
            <Sidebar scale={scaleSidebar} changeScale={setScaleSidebar} activeId={activeId} change={setActiveId} logoText="CryptoParser"/>
          </div>
        )
      } else {
        return(
          <div className='sidebar sidebar__close'>
            <Sidebar scale={scaleSidebar} changeScale={setScaleSidebar} activeId={activeId} change={setActiveId} logoText="CP"/>
          </div>
        )
      }
    }

    return(
      <div className='main'>
          {(page !== "login" && page !== "register") && <HandleScaleSidebar /> }
          <div className='main__info'>
          <IsUserAuth.Provider value={{ isUserAuth, setIsUserAuth }}>
            {(page === "login" || page === "register") && <Link className='link__main' to="/">Назад</Link>}
            <Header theme={theme} change={handleThemeClick}/>
            {/* {page === "home" && <Home />} */}
            {page === "profile" && <Profile />}
            {page === "contacts" && <Contacts />}
            {page === "premium" && <Premium />}
            {page === "partnership" && <Partnership />}
            {page === "error" && <ErrorPage />}
            {page === "login" && <Login />}
            {page === "register" && <Register />}
          </IsUserAuth.Provider>
          </div>
      </div>
    )
};