import React from 'react';
import classes from './Contacts.module.css'


export const Contacts = () => {

    const changeStyle = (titleId, valueId, start) => {
        const title = document.getElementById(titleId)
        const value = document.querySelector(valueId)
        if(start) {
            title.style.transform = "translate(0, -40px)"
            value.style.opacity = "1"
        } else {
            title.style.transform = "translate(0, 0)"
            value.style.opacity = "0"
        }
    }

    const handleStartHover = (e) => {
        const id = e.target.id
        if(id === '0' || id === 'contact__value__tg' || id === "block__child__tg") {
            changeStyle("telegram", "#contact__value__tg", true)
        } else if(id === '1' || id === 'contact__value__ml' || id === "block__child__ml") {
            changeStyle("mail", "#contact__value__ml", true)
        } else if(id === '2' || id === 'contact__value__yt' || id === "block__child__yt") {
            changeStyle("youtube", "#contact__value__yt", true)
        } else if(id === '3' || id === 'contact__value__tgch' || id === "block__child__tgch") {
            changeStyle("telegram__chat", "#contact__value__tgch", true)
        }
    }

    const handleEndHover = (e) => {
        const id = e.target.id
        if(id === '0' || id === 'contact__value__tg' || id === "block__child__tg") {
            changeStyle("telegram", "#contact__value__tg", false)
        } else if(id === '1' || id === 'contact__value__ml' || id === "block__child__ml") {
            changeStyle("mail", "#contact__value__ml", false)
        } else if(id === '2' || id === 'contact__value__yt' || id === "block__child__yt") {
            changeStyle("youtube", "#contact__value__yt", false)
        } else if(id === '3' || id === 'contact__value__tgch' || id === "block__child__tgch") {
            changeStyle("telegram__chat", "#contact__value__tgch", false)
        }
    }

    return(
        <React.Fragment>
            <div className={classes.contacts}>
                <div id={0} onMouseOver={handleStartHover} onMouseOut={handleEndHover} className={[classes.telegram, classes.info__link].join(" ")}>
                    <div id='telegram' className={classes.block__name}>
                        <svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
                            width="26" height="26"
                            viewBox="0 0 48 48">
                            <path fill="#29b6f6" d="M24 4A20 20 0 1 0 24 44A20 20 0 1 0 24 4Z"></path><path fill="#fff" d="M33.95,15l-3.746,19.126c0,0-0.161,0.874-1.245,0.874c-0.576,0-0.873-0.274-0.873-0.274l-8.114-6.733 l-3.97-2.001l-5.095-1.355c0,0-0.907-0.262-0.907-1.012c0-0.625,0.933-0.923,0.933-0.923l21.316-8.468 c-0.001-0.001,0.651-0.235,1.126-0.234C33.667,14,34,14.125,34,14.5C34,14.75,33.95,15,33.95,15z"></path><path fill="#b0bec5" d="M23,30.505l-3.426,3.374c0,0-0.149,0.115-0.348,0.12c-0.069,0.002-0.143-0.009-0.219-0.043 l0.964-5.965L23,30.505z"></path><path fill="#cfd8dc" d="M29.897,18.196c-0.169-0.22-0.481-0.26-0.701-0.093L16,26c0,0,2.106,5.892,2.427,6.912 c0.322,1.021,0.58,1.045,0.58,1.045l0.964-5.965l9.832-9.096C30.023,18.729,30.064,18.416,29.897,18.196z"></path>
                        </svg>
                        <div id="block__child__tg" className={classes.title}>Telegram</div>
                    </div>
                    <div id="contact__value__tg" className={classes.contact__value}>@telegramName</div>
                </div>
                <div id={3} onMouseOver={handleStartHover} onMouseOut={handleEndHover} className={[classes.telegram__chat, classes.info__link].join(" ")}>
                    <div id='telegram__chat' className={classes.block__name}>
                        <svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
                            width="26" height="26"
                            viewBox="0 0 48 48">
                            <path fill="#29b6f6" d="M24 4A20 20 0 1 0 24 44A20 20 0 1 0 24 4Z"></path><path fill="#fff" d="M33.95,15l-3.746,19.126c0,0-0.161,0.874-1.245,0.874c-0.576,0-0.873-0.274-0.873-0.274l-8.114-6.733 l-3.97-2.001l-5.095-1.355c0,0-0.907-0.262-0.907-1.012c0-0.625,0.933-0.923,0.933-0.923l21.316-8.468 c-0.001-0.001,0.651-0.235,1.126-0.234C33.667,14,34,14.125,34,14.5C34,14.75,33.95,15,33.95,15z"></path><path fill="#b0bec5" d="M23,30.505l-3.426,3.374c0,0-0.149,0.115-0.348,0.12c-0.069,0.002-0.143-0.009-0.219-0.043 l0.964-5.965L23,30.505z"></path><path fill="#cfd8dc" d="M29.897,18.196c-0.169-0.22-0.481-0.26-0.701-0.093L16,26c0,0,2.106,5.892,2.427,6.912 c0.322,1.021,0.58,1.045,0.58,1.045l0.964-5.965l9.832-9.096C30.023,18.729,30.064,18.416,29.897,18.196z"></path>
                        </svg>
                        <div id="block__child__tgch" className={classes.title}>Telegram chat</div>
                    </div>
                    <div id="contact__value__tgch" className={classes.contact__value}>@telegramChat</div>
                </div>
                <div id={2} onMouseOver={handleStartHover} onMouseOut={handleEndHover} className={[classes.youtube, classes.info__link].join(" ")}>
                    <div id="youtube" className={classes.block__name}>
                    <ion-icon name="logo-youtube"></ion-icon>
                        <div id="block__child__yt" className={classes.title}>YouTube</div>
                    </div>
                    <div id="contact__value__yt" className={classes.contact__value}>channel</div>
                </div>
                <div id={1} onMouseOver={handleStartHover} onMouseOut={handleEndHover} className={[classes.mail, classes.info__link].join(" ")}>
                    <div id="mail" className={classes.block__name}>
                        <ion-icon name="mail"></ion-icon>
                        <div id="block__child__ml" className={classes.title}>Mail</div>
                    </div>
                    <div id="contact__value__ml" className={classes.contact__value}>12345@mail.ru</div>
                </div>
            </div>
        </React.Fragment>
    )
};