import React from 'react';
import { Avatar } from '@mui/material';
import { deepOrange } from '@mui/material/colors';
import { TextField } from '@mui/material';
import { EditBtn } from '../../../UI/editbtn/EditBtn';
import { useContext } from 'react';
import { ShowModalContext } from '../../../context';
import { useEffect } from 'react';
import { AlertModal } from '../../../UI/modal/AlertModal';

export const UserInfo = () => {

    const {show, setShow} = useContext(ShowModalContext)

    const handleShow = () => {
        setShow(true)
    }

    return(
        <React.Fragment>
            <AlertModal />
            <div className='profile__title'>личные данные</div>
            <div className='user__info__main'>
                <div className='user__info'>
                    <div className='avatar'>
                        <Avatar sx={{ bgcolor: deepOrange[500] }}>П</Avatar><span>Павел</span>
                    </div>
                    <div className='user__info__all'>
                        <div className='user__info__titles'>
                            <div>Почта</div>
                            <div>Пароль</div>
                            <div>Telegram</div>
                        </div>
                        <div className='user__info__fields'>
                            <div className='user__info__point'><TextField value="somemail123@mail.ru" id="standard-basic" size='small' variant="standard" /></div>
                            <div className='user__info__point'><TextField value="213dfsaf32" id="standard-basic" type="password" size='small' variant="standard" /><div onClick={handleShow}><EditBtn /></div></div>
                            <div className='user__info__point'><TextField value="@telegramName" id="standard-basic" size='small' variant="standard" /></div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )  
};