import React from 'react';
import classes from './LoginRegister.module.css'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Button } from '@mui/material';
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { Alert } from '@mui/material';
import { useState } from 'react';

export const Register = () => {

    const {
        register,
        formState: {
            errors
        },
        handleSubmit
    } = useForm({
        mode: "onChange"
    });

    const onSubmit = async (data) => {
        alert(JSON.stringify(data));
    }

    const [telegramUser, setTelegramUser] = useState("");
    const handleChangeTelegram = (e) => {
        setTelegramUser((e.target.value).replace("@", ""));
    }
    const telegramLink = "https://telegram.dog/";

    return(
        <div className={classes.register__main}>
            <div className={classes.register__form}>
                <div className={classes.register__title}>Register</div>
                    <Box
                        onSubmit={handleSubmit(onSubmit)}
                        component="form"
                        sx={{
                            '& .MuiTextField-root': { m: 1, width: '25ch' },
                        }}
                        noValidate
                        autoComplete="off"
                    >
                        <div className={classes.register__fields}>
                            <TextField
                            className='user__info__point'
                            id="outlined-required"
                            label="Логин"
                            {...register("login", {required: true, minLength: 4, pattern: /[0-9a-zA-Z!@#$%^&*]{4,}/i})}
                            />
                            <TextField
                            className='user__info__point'
                            id="outlined-required"
                            label="Имя" 
                            {...register("name", {required: true, maxLength: 20})}
                            />
                            <TextField
                            className='user__info__point'
                            id="outlined-password-input"
                            label="Пароль"
                            type="password"
                            autoComplete="current-password"
                            {...register("password", {required: true, minLength: 8, pattern: /[0-9a-zA-Z]{8,}/i})}
                            />
                            {/* <TextField
                            className='user__info__point'
                            id="outlined-password-input"
                            label="Повтор пароля"
                            type="password"
                            autoComplete="current-password"
                            {...register("password_repeat", {required: true})}
                            /> */}
                            <TextField
                            className='user__info__point'
                            id="outlined-password-input"
                            label="Почта"
                            type="mail"
                            autoComplete="current-password"
                            {...register("mail", {required: true, pattern: /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i})}
                            />
                            <TextField
                            className='user__info__point'
                            id="outlined-password-input"
                            label="Ник в Telegram"
                            autoComplete="current-password"
                            value={"@" + telegramUser}
                            {...register("tgname", {required: true, pattern: /[0-9a-zA-Z]/i})}
                            placeholder="@telegram"
                            onChange={handleChangeTelegram}
                            />
                            <div class={classes.telegram__link}><a target="_blank" rel="noreferrer" href={telegramLink + telegramUser.replace("@", "")}><ion-icon name="arrow-redo-outline"></ion-icon></a></div>
                        </div>
                        <div className={classes.submit__register}>
                            <Button type="submit" style={{ marginTop: "20px" }} variant="outlined">Регистрация</Button>
                            <Link
                            to="/login"
                            style={{ 
                                textDecoration: "none",
                                color: "#1976d2",
                                marginTop: "20px",
                                fontFamily: "'Inria Sans', sans-serif",
                                fontSize: "14px"
                            }}
                            >У меня есть аккаунт</Link>
                        </div>
                        <div className={classes.error_messages}>
                            <div>{errors?.login && <Alert severity="error">{errors?.login?.message || "Логин должен состоять из латинских букв длиной не менее 4 символов"}</Alert>}</div>
                            <div>{errors?.name && <Alert severity="error">{errors?.name?.message || "Имя не может быть пустым"}</Alert>}</div>
                            <div>{errors?.password && <Alert severity="error">{errors?.password?.message || "Пароль должен состоять из латинских букв и цифр длиной не менее 8 символов"}</Alert>}</div>
                            <div>{errors?.mail && <Alert severity="error">{errors?.mail?.message || "Неверный формат почты"}</Alert>}</div>
                            <div>{errors?.tgname && <Alert severity="error">{errors?.tgname?.message || "Поле Telegram не может быть пустым"}</Alert>}</div>
                        </div>
                    </Box> 
            </div>
        </div>
    )
};