import React from 'react';
import classes from './AlertModal.module.css';
import { useContext } from 'react';
import { ShowModalContext } from '../../context';
import { CSSTransition } from 'react-transition-group';
import { useState } from 'react';
import { TextField, Button, Input, Alert, CloseIcon, IconButton } from '@mui/material';
import PasswordStrengthBar from 'react-password-strength-bar';
import { useEffect } from 'react';
import { useRef } from 'react';


export const AlertModal = () => {


    const {show, setShow} = useContext(ShowModalContext)
    const [stage, setStage] = useState("isSendMsg")
    const [currentPassword, setCurrentPassword] = useState("")
    const [currentCode, setCurrentCode] = useState("")
    const [passwordStage, setPasswordStage] = useState("") 
    const [isBtnActive, setIsBtnActive] = useState(false)
    const [showAlert, setShowAlert] = useState(false)
    const activeBtn = useRef(null)

    const codeFromMsg = 123456;

    const handleChangeStage = () => {
        const newStage = stage === "isSendMsg" ? "codeInput" : "setPassword";
        if(stage === "codeInput") {
            if(parseInt(currentCode) === codeFromMsg) {
                console.log("succes")
                setStage(newStage);
            } else {
                const errorField = document.querySelector("#password-error")
                errorField.innerHTML = "Неверный код!"
            }
        } else if(stage === "setPassword") {
            setShow(false);
            setShowAlert(true)
        } else {
            setStage(newStage);
        }
    }

    const handleCloseModal = () => {
        setShow(false)
    }

    useEffect(()=>{
        if(passwordStage === 3 || passwordStage === 4){
            setIsBtnActive(true);
        } else {
            setIsBtnActive(false);
        }
    }, [passwordStage])

    return(
        <div>
            {show && <div onClick={handleCloseModal} className={classes.back__shadow}>
                    { stage === "isSendMsg" &&
                    <div className={classes.form} onClick={(e)=>{e.stopPropagation()}}>
                        <div className={classes.title}>Смена пароля</div>
                        <div className={classes.message}>Прислать код для смены пароля на почту?<p>somemail123@mail.ru</p></div>
                        <Button onClick={handleChangeStage} className={classes.button} variant="outlined">Отправить</Button>
                    </div> 
                    }
                    { stage === "codeInput" &&
                    <div className={classes.form} onClick={(e)=>{e.stopPropagation()}}>
                        <div className={classes.title}>Смена пароля</div>
                        <TextField
                            className='user__info__point'
                            inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                            id="standard-number"
                            label="6-значный код"
                            type="number"
                            value={currentCode}
                            onChange={(e)=>{setCurrentCode(e.target.value)}}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            variant="standard"
                        />
                        <div id="password-error" className={classes.error}></div>
                        <Button onClick={handleChangeStage} className={classes.button} variant="outlined">Отправить</Button>
                    </div> 
                    }
                    { stage === "setPassword" &&
                    <div className={classes.form} onClick={(e)=>{e.stopPropagation()}}>
                        <div className={classes.title}>Смена пароля</div>
                        <TextField
                            className='user__info__point'
                            id="standard-number"
                            label="Новый пароль"
                            variant="standard"
                            value={currentPassword}
                            onChange={(e)=>{setCurrentPassword(e.target.value)}}
                        />
                        <PasswordStrengthBar
                            password={currentPassword}
                            style={{ width: "180px" }}
                            scoreWords={["Слабый", "Слабый", "Средний", "Хороший", "Отличный"]}
                            shortScoreWord="Слишком короткий"
                            minLength={6}
                            onChangeScore={(score)=>{setPasswordStage(score)}}
                        />
                        <Button ref={activeBtn} onClick={handleChangeStage} disabled={!isBtnActive} className={classes.button} variant="outlined">Сохранить</Button>
                    </div> 
                    }    
            </div>}
            {showAlert &&
                <Alert color="info" severity="success" className={classes.alert__msg} onClose={() => {setShowAlert(false)}}>Пароль успешно изменён</Alert>
            }
        </div>
    )  
};