import React from 'react';
import { useState } from 'react';
import classes from './Premium.module.css'
import { FormControl, InputLabel, Select, MenuItem, TextField, Button } from '@mui/material';

export const Premium = () => {

    const [payment, setPayment] = useState('');
    const [subType, setSubType] = useState('')

    const handleChange = (event) => {
        setPayment(event.target.value);
    };

    const handleChangeSub = (event) => {
        setSubType(event.target.value)
        const btns = document.querySelectorAll("#btn__choose__sub")
        Array.from(btns).map(e=>e.removeAttribute("disabled"))
        event.target.setAttribute("disabled", "disabled")
    }


    return(
        <React.Fragment>
            <div className={classes.premium__list}>
                <div className={classes.premium__weak__twoweak}>
                    <div className={classes.premium__title}>7 дней</div>
                    <div className={classes.premium__description}>-----</div>
                    <div className={classes.premium__buy}>
                        <div className={classes.premium__price}>20$</div>
                        <div className={classes.premium__buy__btn}>
                            <button id="btn__choose__sub" value="7 дней" onClick={handleChangeSub}>Оплатить</button>
                        </div>
                    </div>
                </div>
                {/* <div className={classes.buy__form}>
                    <div>
                        <ion-icon name="ribbon-outline"></ion-icon>
                    </div>
                    <div style={{ fontSize: "20px", textTransform: "uppercase", fontWeight: "bold", margin: "20px 0 50px 0" }}>Премиум</div>
                    <FormControl sx={{ minWidth: 200 }}>
                        <InputLabel sx={{ color: "#7889AE", fontSize: "14px" }} id="demo-simple-select-label">Оплата</InputLabel>
                        <Select
                            sx={{ color: "#7B88AE" }}
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={payment}
                            label="Оплата"
                            onChange={handleChange}
                        >
                            <MenuItem value={30}>Криптовалюта</MenuItem>
                        </Select>
                        <TextField
                            style={{ marginTop: "20px" }}
                            sx={{ color: "#7B88AE"}}
                            className={classes.select__active}
                            InputProps={{
                                readOnly: true,
                            }}
                            readOnly
                            id="outlined-basic"
                            value={subType !== '' ? subType : 'Тариф'}
                            variant="outlined"
                        />
                        <Button style={{ marginTop: "20px" }} variant="outlined" size="medium">
                            Оплатить
                        </Button>
                    </FormControl>
                </div> */}
                <div className={classes.premium__weak__twoweak}>
                    <div className={classes.premium__title}>14 дней</div>
                    <div className={classes.premium__description}>-----</div>
                    <div className={classes.premium__buy}>
                        <div className={classes.premium__price}>35$</div>
                        <div className={classes.premium__buy__btn}>
                            <button id="btn__choose__sub" value="14 дней" onClick={handleChangeSub}>Оплатить</button>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
};