import React from 'react';
import classes from './SidebarBtn.module.css'

export const SidebarBtn = ({text, name, id, activeId, change}) => {
    const classList = [classes.sidebar__btn, "small"];

    const newValue = () => {
        change(id)
    };

    const MenuPoint = () => {
        classList.push(classes.sidebar__btn__active);

        return(
            <React.Fragment>
                <div id={id} onClick={newValue} className={classList.join(' ')}>
                    <ion-icon class="sidebar__ionicon" name={name}></ion-icon>
                    <span id={id}>{text}</span>
                </div>
                {/* {activeId == 6 && <ProfileMenu />} */}
            </React.Fragment>
        )
    }

    const MenuPointUnActive = () => {
        return(
            <div id={id} onClick={newValue} className={classList.join(' ')}>
                <ion-icon class="sidebar__ionicon" name={name}></ion-icon>
                <span id={id} className={classes.text}>{text}</span>
            </div>
        )
    }

    const ResultMenu = () => {
        if(id === activeId) {
            return(
                <MenuPoint />
            )
        } else{
            return(
                <MenuPointUnActive />
            )
        }
    }

    return(
        <ResultMenu />
    )   
}