import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import classes from './ThemeHandler.module.css'

export const ThemeHandler = ({change}) => {

    const classList = [classes.handler];

    const handleThem = (e) => {
        change()
        const theme = localStorage.getItem("app-theme") || "light"
        if(e.target.getAttribute("name") === "invert-mode-outline") {
            theme === "light" ? e.target.parentNode.style.justifyContent = "flex-end" : e.target.parentNode.style.justifyContent = "flex-start";
        } else {
            theme === "light" ? e.target.style.justifyContent = "flex-end" : e.target.style.justifyContent = "flex-start";
        }
    };

    useEffect(()=>{
        const theme = localStorage.getItem("app-theme") || "light";
        const e = document.getElementById("handler__btn");
        theme === "light" ? e.style.justifyContent = "flex-start" : e.style.justifyContent = "flex-end";
    }, [])

    return(
        <div id="handler__btn" name='handler' onClick={handleThem} className={classList.join(" ")}>
            <ion-icon class="handler__img" name="invert-mode-outline"></ion-icon>
        </div>
    )  
};